import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate' // vuex数据持久化

Vue.use(Vuex)

// node服务器域名
const middleServerPort = location.protocol ==='https:' ? ':5001' : ':5000';
const domainNodeServer = location.port ? (location.hostname + middleServerPort) : location.host;
// 单独定义初始值模板
const stateTpl = {
  token: '',
  user: {},
  domainApi: (location.host === "doudizhu.github.io") ? 'api.daokoujihua.com' : '', // api服务器域名
  domainNodeServer,
  originNodeServer : `${location.protocol}//${domainNodeServer}`,  // node服务器url
}
// console.log('stateTpl:',stateTpl)

export default new Vuex.Store({
  state: {
    ...stateTpl,
  },
  mutations: {
    setToken(state, token){ 
      state.token = token
    },
    setUser(state, user){ 
      state.user = user
    },
    setDomainApi(state, domainApi){ 
      state.domainApi = domainApi
    },
    setDomainNodeServer(state, domainNodeServer){ 
      state.domainNodeServer = domainNodeServer
    },
    setOriginNodeServer(state, originNodeServer){ 
      state.originNodeServer = originNodeServer
    },

    // 重置vuex
    resetAll(state) {
      Object.assign(state, stateTpl) // 要用Object.assign而不是直接给state赋值。  
    }
  },
  actions: {

  },
  plugins: [
    createPersistedState(),
  ],
})
