import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import store from './store'
import { Toast } from 'cube-ui'

Vue.use(Router)

/**
 * meta:{
 *  requireAuth: false, // 不需要登录访问,其余情况默认需要登录访问
 * }
 */
export const routes = [
    {
      path: '/',
      // name: 'home',
      // component: Home
      redirect: '/project/list',
    },
    {
      path: '/user',
      component: { render(c: any) { return c('router-view') } },
      children: [
        {
          path: 'login',
          component: () => import('./views/user/Login.vue'),
          name: 'userLogin',
          meta: {
            title: '登录',
            requireAuth: false, // 不需要登录访问
          },
        },
        {
          path: 'register',
          component: () => import('./views/user/Register.vue'),
          meta: {
            title: '注册',
            requireAuth: false, // 不需要登录访问
          },
        },
        {
          path: 'mine',
          component: () => import('./views/user/Mine.vue'),
          meta: {
            title: '我的',
          },
        },
        {
          path: 'invested',
          component: () => import('./views/user/Invested.vue'),
          meta: {
            title: '已投项目',
          },
        },
        {
          path: 'coupon',
          component: () => import('./views/user/Coupon.vue'),
          meta: {
            title: '优惠券',
          },
        },
      ]
    },
    {
      path: '/account',
      component: { render(c: any) { return c('router-view') } },
      children: [
        {
          path: 'lmAccount',
          component: () => import('./views/account/LmAccount.vue'),
          meta: {
            title: '资金账户',
          },
        },
        {
          path: 'lmCharge',
          component: () => import('./views/account/LmCharge.vue'),
          meta: {
            title: '充值',
          },
        },
        {
          path: 'lmWithdraw',
          component: () => import('./views/account/LmWithdraw.vue'),
          meta: {
            title: '提现',
          },
        },
      ]
    },
    {
      path: '/project',
      component: { render(c: any) { return c('router-view') } },
      children: [
        {
          path: 'list',
          component: () => import('./views/project/List.vue'),
          meta: {
            title: '项目列表',
            requireAuth: false, // 不需要登录访问
          },
        },
        {
          path: 'detail/:id',
          component: () => import('./views/project/Detail.vue'),
          meta: {
            title: '项目详情',
            requireAuth: false, // 不需要登录访问
          },
        },
        {
          path: 'desc/:id',
          component: () => import('./views/project/Desc.vue'),
          meta: {
            title: '信息披露',
            requireAuth: false, // 不需要登录访问
          },
        },
        {
          path: 'report/:id',
          component: () => import('./views/project/Report.vue'),
          meta: {
            title: '项目回报',
            requireAuth: false, // 不需要登录访问
          },
        },
        {
          path: 'order/:id',
          component: () => import('./views/project/Order.vue'),
          meta: {
            title: '项目订单',
          },
        },
        {
          path: 'investReturn',
          component: () => import('./views/project/InvestReturn.vue'),
          meta: {
            title: '出借结果',
          },
        },
        {
          path: 'introduction',
          component: () => import('./views/project/Introduction.vue'),
          meta: {
            title: '平台介绍',
            requireAuth: false, // 不需要登录访问
          },
        },
      ]
    },
    {
      path: '*',
      redirect: location.host === "doudizhu.github.io" ? '/user/login' : '',
      name: '/404',
      component: ()=>import('./views/404.vue'),
      meta: {
        title: '404',
        requireAuth: false, // 不需要登录访问
      },
    },
    {
      path: '/test',
      component: () => import('./views/Test.vue'),
      meta: {
        title: 'test',
        requireAuth: false, // 不需要登录访问
      },
    },
    {
      path: '/test/uploadIdCard',
      component: () => import('./components/views/account/lmAccount/UploadIdCard.vue'),
    }
  ]

const router = location.host === "doudizhu.github.io"
  ? new Router({
    base: process.env.BASE_URL,
    routes,
  })
  : new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
  })


// 路由守卫
router.beforeEach((to,from,next)=>{
  /**设置每个页面的title */
  if (to.meta.title) {
    document.title = to.meta.title;
  }
  const isLogin = store.state.token ? true : false;
  /**页面路由守卫：判断：是否需要登录访问 */
  if (to.meta.requireAuth === false) { // 不需要登录访问
    next()
  } 
  else { // 需要登录访问
    if (isLogin) {
      next()
    } else {
      (Toast as any).$create({
        type: 'txt',
        txt: '登录状态失效，请重新登录',
      }).show()
      store.commit('resetAll')
      next({
        path: '/user/login',
        query: {redirect: to.fullPath},
      })
    }
  }
  
})  
export default router
