import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import store from './store'
import { Toast } from 'cube-ui'

Vue.use(Router)

/**
 * meta:{
 *  requireAuth: false, // 不需要登录访问,其余情况默认需要登录访问
 * }
 */
export const routes = [
    {
      path: '/',
      redirect: '/financing'
    },
    {
      path: '/financing',
      name: 'financing',
      component: () => import('./views/Financing.vue'),
      meta: {
        title: '我要融资',
        requireAuth: false, // 不需要登录访问
      },
    },
  ]
// 多页面history兼容(开发环境)
const projectNamePathIndex = (location.pathname).indexOf('.html')
let base = process.env.BASE_URL;
if(projectNamePathIndex >= 0 ){
  base = `/${location.pathname.slice(1,projectNamePathIndex)}.html`
}

console.log(base)
const router = location.host === "doudizhu.github.io"
  ? new Router({
    base,
    routes,
  })
  : new Router({
    mode: 'history',
    base,
    routes,
  })


// 路由守卫
router.beforeEach((to,from,next)=>{
  /**设置每个页面的title */
  if (to.meta.title) {
    document.title = to.meta.title;
  }
  const isLogin = store.state.token ? true : false;
  /**页面路由守卫：判断：是否需要登录访问 */
  if (to.meta.requireAuth === false) { // 不需要登录访问
    next()
  }
  else { // 需要登录访问
    if (isLogin) {
      next()
    } else {
      (Toast as any).$create({
        type: 'txt',
        txt: '登录状态失效，请重新登录',
      }).show()
      store.commit('resetAll')
      next({
        path: '/user/login',
        query: {redirect: to.fullPath},
      })
    }
  }

})
export default router
